import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { ArrowLeft } from "lucide-react"

const CallRenewalPage = () => {
    return (
        <div className='p-6 bg-gray-50 min-h-screen'>
            <div className="max-w-6xl mx-auto">
                {/* CLIENT INFO (MAYBE LIKE A NAVBAR?) */}
                <div className="flex items-center gap-3 mb-6">
                    <Button variant="outline" size="icon">
                        <ArrowLeft className="w-4 h-4" />
                    </Button>
                    <h1 className="text-3xl font-bold">Renewal Profile</h1>
                </div>

                <div className="grid grid-cols-3 gap-6">
                    {/* LEFT COLUMN - MAIN INFO */}
                    <div className="col-span-2 space-y-6">
                        <Card className="bg-primary-foreground border-0 shadow-sm">
                            <CardContent className="p-6">
                                <div className="flex items-start justify-between mb-6">
                                    <div className="flex items-start gap-4">
                                        <div className="w-16 h-16 rounded-full relative overflow-hidden">
                                            "Client Name"
                                        </div>
                                    </div>
                                </div>
                            </CardContent>
                        </Card>
                    </div>
                </div>

                {/* CLIENT RENEWAL HISTORY */}
                <div className="">test</div>

                {/* IMPORTANT FINANCIAL INFO */}
                <div className="">test</div>

                {/* GRAPHS (MAYBE) */}
                <div className="">test</div>

                {/* QUICK ACTIONS TO DO FOR THIS CLIENT */}
                <div className="">test</div>
            </div>
        </div>
    )
}

export default CallRenewalPage

/*

Splitting main content up this way. Main content is 2/3s of the size while secondary content takes up 1/3 of the space.
For client info, can make that into part of the main content or into a header of sorts depending on if we keep navbar.

*/